<h2 id="popup-shu-xing"><a class="header-anchor" href="#popup-shu-xing" aria-hidden="true">¶</a> Popup 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>popup-container</td>
<td>指定弹出框的父元素(可以减小父容器样式的影响)</td>
<td>function</td>
<td>-</td>
<td>()=&gt;document.body</td>
</tr>
<tr>
<td>placement</td>
<td>弹出框相对显示框的位置</td>
<td>string</td>
<td>'top/top-start/top-end/bottom/bottom-start/bottom-end/left/left-start/left-end/right/right-start/right-end'</td>
<td>top</td>
</tr>
</tbody>
</table>
<h2 id="popup-slots"><a class="header-anchor" href="#popup-slots" aria-hidden="true">¶</a> Popup slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>插入显示框</td>
</tr>
<tr>
<td>popup</td>
<td>插入弹出框</td>
</tr>
</tbody>
</table>
</section></template>
<style>
    .demo-popup{
        margin-bottom:2em;
    }
    .demo-content{
        background-color: rgba(70,76,91,.9);
        color:#fff;
        padding: 0.8em 1.5em;
        border-radius: 4px;

    }
</style>
<script>
    export default{
        data(){
            return{
                visiable:false
            }
        },
        methods:{
          container(){
            return document.getElementById('container')
          }
        }
    }
</script>


